.site-menu {
    padding: 0;
    font-family: @font-heading;
    line-height: 1;
    text-transform: uppercase;
    list-style: none;

    @media (max-width: @breakpoint-small) {
        font-size: 20px;
        font-weight: 500;
        text-align: center;
    }

    @media (min-width: @breakpoint-medium) {
        display: flex;
        justify-content: flex-end;
        font-size: 18px;
    }
}

.site-menu__link {
    color: @color-black;
    text-decoration: none;
}
