.page-header__nav {
    @media (min-width: @breakpoint-medium) {
        width: 708px;
        margin: 0 auto;
        display: grid;
        grid-template-columns: 174px 1fr;
        gap: 16px;
    }

    @media (min-width: @breakpoint-large) {
        width: 1220px;
    }
}

.page-header__bar {
    @media (max-width: @breakpoint-small) {
        width: 320px;
        margin: 0 auto;
        padding: 10px 20px 18px;
        display: grid;
        grid-template-columns: 34px 1fr 34px;
        gap: 16px;
    }
}

.page-header__logo {
    grid-column: 1 / 3;
}

.page-header__menu {
    margin: 0;
}

.page-header__toggle {
    @media (min-width: @breakpoint-medium) {
        display: none;
    }
}
