.contacts {
    padding: 20px;
}

.contacts__container {
    display: grid;
    grid-template-columns: 1fr 1fr;
}

.contacts__heading {
    font-family: @font-heading;
    font-size: 16px;
    line-height: 20px;
    text-transform: uppercase;
}

.contacts__address {
    color: @color-gray-700;
    line-height: 20px;
}
